.mainBlock{
  display: flex;
  float: right;
  z-index: 99;
}
.navbar{
  display: flex;
  background-color: #fff;
  height: 72px;
  padding: 0 30px;
  z-index: 99;
  width: 100%;
  box-shadow: 0 2px 4px #00000026;
  box-sizing: border-box;
  letter-spacing: 0;
  outline: 0;
}

.links{
  display: flex;
  height: 72px;
  width: 100%;
}
.heading{
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 15px;
  color: #4a4a4a;
  height: inherit;
}
.uploadContestButton{
  display: flex;
  margin-top: 20px;
  padding: 8px 8px;
  color: white;
  font-size: 1.25rem;
  cursor: pointer;
  border: transparent;
  border-radius: 0.25rem;
  font-weight: 700;
  height: 35px;
  letter-spacing: 1px;
  overflow: visible;
  font-family: "Cabin", Sans-Serif;
  font-size: 100%;
  line-height: 1.15;
  background-color: #4a4a4a;
  margin-left: 10px;
}
.uploadContestButton:hover{
  background-color: #696363;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transition: 300ms ease-in-out all;
}
.linkTextButton{
  text-decoration: none;
}
.logoutButton{
  float: right;
  margin-top: -52px !important;
  margin-right: 40px !important;
}
.logoutButtonLink{
  text-decoration: none;
}

